@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
    margin: 0 !important;
    font-family: 'Frank Ruhl Libre', serif !important;
    background-color: rgb(226, 226, 226);
}
#body{
    display:flex;
    flex-direction: column;
    align-items: center;
} 
.view-box{
    display:flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: white;
    width: 100%;
}
.view-text{
    padding-left: 10px;
    font-size: 20px;
    margin: 15px 0;
}
.search-box{
    justify-self: right;
    display:flex;
    align-items: center;
    padding-right: 10px;
}
#loading-box{
    height: 85vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
#empty-box{
    height: 85vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
#empty-text{
    font-size: 30px;
}
.search-input{
    height: 20px;
}
.search-text{
    justify-self: flex-end;
    padding-left: 10px;
    font-size: 20px;
    margin: 15px 0;
    margin-right: 5px;
}
.article-image{
    border-radius: 5px;
    width: 100%;
    grid-area:image;
}
.article-title{
    padding:10px;    
    padding-bottom: 5px;
    grid-area:title;  
    font-size: 30px;  
    margin-top: 8px;
    margin-bottom: 0;
}
.article-author{
    font-style: italic;
    padding:10px;
    padding-top: 0;
    padding-bottom: 0;
    font-size: 18px;
    margin-top: 5px;
    color: rgb(54, 54, 54);
    grid-area:author;
}
.article-description{
    padding:10px;
    font-size: 20px;
    margin-top: 0px;
    margin-bottom: 5px;
    grid-area:description;
}
#article-box{
    display:flex;
    flex-direction: column;
    align-items: center;
}
.article{
    border-radius: 10px;
    cursor: pointer;
    background-color: white;
    /* border:1px solid rgb(54, 18, 18); */
    margin: 10px 0 10px 0;
    width: 70%;
    display:grid;
    grid-template-areas: 
    "image"
    "title"
    "author"
    "description";
}
@media only screen and (min-width: 1000px){
    .article{
        width: 40%;
    }
}
@media only screen and (max-width: 600px){
    .article{
        width: 100%;
    }
    .article-image{
        border-radius: 0; 
    }
}

